import { LitElement, html, css } from "lit";

export class Header extends LitElement {
    constructor() {
        super();
    }

    static styles = css`
        div {
            float: left;
            width: 100%;
            background-color: dodgerblue;
            color: antiquewhite;
        }

        h1 {
            float: left;
        }

        #cast {
            float: right;
            width: 50px;
            margin-top: 20px;
        }
    `;

    render() {
        return html`
            <div>
                <h1>Azure TV</h1>
                <google-cast-launcher id="cast"></google-cast-launcher>
            </div>
        `;
    }
}
customElements.define('app-header', Header);