import * as msal from "@azure/msal-browser";
import { BlobServiceClient } from "@azure/storage-blob";
import db from './db.json';
import { Poster } from "./components/Poster";
import { Header } from "./components/Header";

const msalConfig = {
    auth: {
        clientId: '53bfecae-5150-4a44-8b5b-957ac4839fa4',
        authority: 'https://login.microsoftonline.com/4583a017-e1c7-4872-bb63-71c8f247fb02'
    }
};

const msalInstance = new msal.PublicClientApplication(msalConfig);

// need to handle the redirect promise when using the redirect login instead of popup
msalInstance.handleRedirectPromise().then(async (tokenResponse) => {
    // Check if the tokenResponse is null
    // If the tokenResponse !== null, then you are coming back from a successful authentication redirect. 
    // If the tokenResponse === null, you are not coming back from an auth redirect.
    if(tokenResponse) {
        // console.log(tokenResponse);
        // const myAccounts = msalInstance.getAllAccounts();
        // console.log(myAccounts);

        const tokenCredential = {
            getToken() {
                return {
                    token: tokenResponse.accessToken,
                    expiresOnTimestamp: Date.now() + 60 * 60 * 1000,
                };
            }
        };

        const blobServiceClient = new BlobServiceClient("https://azuretv.blob.core.windows.net/", tokenCredential);
        // console.log(blobServiceClient);

        // blobServiceClient.setProperties({
        //     defaultServiceVersion: "2021-04-10"
        // }).then(res => console.log("Set Properties response", res))
        // .catch(err => console.error("Set Properties error", err));

        // blobServiceClient.getProperties()
        //     .then(res => console.log("Get Properties response", res))
        //     .catch(err => console.error("Get Properties error", err));

        const containerClient = blobServiceClient.getContainerClient("media");
        // console.log(containerClient);

        // get all blobs
        // for await (const blob of containerClient.listBlobsFlat()) {
        //     console.log(blob);

        //     const blobClient = containerClient.getBlockBlobClient(blob.name);
        //     const meta = (await blobClient.getProperties()).metadata;
        //     console.log(meta);

        //     addShowPlayButton(blob, meta);
        // }

        // get just TV shows
        for await (const item of containerClient.listBlobsByHierarchy("/", { prefix: "TV/" })) {
            console.log(item);
            if(item.kind === "prefix") {
                console.log(`\tBlobPrefix: ${item.name}`);
                // TODO - Revise folder structure, probably only need tmdbid as the parent folder e.g.
                // TV/3111/blah.m4v
                const tmdbId = item.name.split("{")[1].replace("}", "").replace("/", "");
                const showData = db[tmdbId];
                
                const libraryDiv = document.getElementById("library");
                const poster = document.createElement('show-poster');
                poster.src = `https://image.tmdb.org/t/p/w92/${showData.poster_path}`;
                poster.id = showData.id;
                libraryDiv.appendChild(poster);

            } else {
                console.log(`\tBlobItem: name - ${item.name}, last modified - ${item.properties.lastModified}`);
            }
        }


    } else {
        try {
            const loginRequest = {
                scopes: ["https://storage.azure.com/user_impersonation"]
            };
            msalInstance.loginRedirect(loginRequest);
        } catch (err) {
            // handle error
            console.error(err);
        }
    }
}).catch((error) => {
    // handle error, either in the library or coming back from the server
    console.error(error);
});


const addShowPlayButton = (blob, meta) => {
    const libraryDiv = document.getElementById("library");
    const showDiv = document.createElement("div");
    const showNamePara = document.createElement("p");
    showNamePara.textContent = meta.title;

    console.log(`https://azuretv.blob.core.windows.net/media/${blob.name}`);
    console.log(blob.properties.contentType);

    const playBtn = document.createElement("button");
    playBtn.innerHTML = "Play";
    playBtn.onclick = () => {
        const castSession = cast.framework.CastContext.getInstance().getCurrentSession();
        const metadata = new chrome.cast.media.TvShowMediaMetadata();
        metadata.episode = meta.episode;
        // metadata.images = [
        //     "https://m.media-amazon.com/images/M/MV5BMTczMDEwNzY2Nl5BMl5BanBnXkFtZTYwNjg3NzA5._V1_.jpg"
        // ];
        // metadata.originalAirdate = "1999-07-05";
        metadata.season = meta.season;
        metadata.title = meta.title;
        const mediaInfo = new chrome.cast.media.MediaInfo(
            `https://azuretv.blob.core.windows.net/media/${blob.name}`,
            // "https://dghwarehousestrapi.blob.core.windows.net/dgh-warehouse-strapi/1-BackToSchool.m4v",
            blob.properties.contentType);
        mediaInfo.metadata = metadata;
        const request = new chrome.cast.media.LoadRequest(mediaInfo);
        castSession.loadMedia(request)
            .then(() => { 
                console.log('Load succeeded');
            }).catch((err) => {
                console.error('Error code', err);
            });
    };

    showDiv.appendChild(showNamePara);
    showDiv.appendChild(playBtn);
    libraryDiv.appendChild(showDiv);
};